export default class Geleijingsai extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        各类竞赛获奖
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='兰新武' data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
