import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/teacher/linyin/课程数（单位：门数，三年）.json'
export default class Kechengshu extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        课程数（单位：门数，三年）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='林崟' data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
