import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/teacher/zhuxianming/督导评价.json'
export default class Dudaopingjia extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        督导评价
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='朱先明' data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
