import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/teacher/zhuxianming/课堂活跃度.json'
export default class Ketanghuoyuedu extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        课堂活跃度
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='朱先明' data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
