export default class Xueshengpingjia extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        学生评价
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='袁娟' data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
