import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/teacher/linyin/出版专著（独著、合著，三年）.json'
export default class Chubanzhuanzhu extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        出版专著
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='林崟' data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
